window.$ = window.jQuery = function (selectorOArray) {
    let elements;
    if (typeof selectorOArray === "string") {
        elements = document.querySelectorAll(selectorOArray);
    } else if (selectorOArray instanceof Array) {
        elements = selectorOArray;
    }
    const api = Object.create(jQuery.prototype);//Áõ∏ÂΩì‰∫éÔºöconst api={__proto__:jQuery.prototype};
    // api.elements = elements;
    // api.oldApi = selectorOArray.oldApi;
    //ÂèØ‰ª•ÁÆÄÂÜôÔºöÊµÖÂ§çÂà∂
    Object.assign(api,{
        elements : elements,
        oldApi : selectorOArray.oldApi
    })
    return api;
}


jQuery.prototype = {
    constructor: jQuery,
    addClass(className) {
        for (let i = 0; i < this.elements.length; i++) {
            this.elements[i].classList.add(className);
        }
        return this;
    },
    find(selector) {
        let array = [];
        for (let i = 0; i < this.elements.length; i++) {
            array = array.concat(Array.from(this.elements[i].querySelectorAll(selector)));
        }
        array.oldApi = this;
        return jQuery(array);
    },
    end() {
        return this.oldApi;
    },
    each(fn) {
        for (let i = 0; i < this.elements.length; i++) {
            fn.call(null, this.elements[i], i)
        }
        return this;
    },
    parent() {
        const array = [];
        this.each((node) => {
            if (array.indexOf(node.parentNode) === -1) {//Âà§Êñ≠‰∏Ä‰∏ãÊòØ‰∏çÊòØÂ∑≤ÁªèÊâæÂà∞‰∫ÜÔºåÊúâÊó∂ÂÄô‰ºöÊâæÂ•ΩÂ§öÊ¨°
                array.push(node.parentNode);
            }
        })
        return jQuery(array);
    },
    //ÊâìÂç∞Âá∫
    print() {
        console.log(this.elements);
    },
    children() {
        const array = [];
        this.each((node) => {
            array.push(...node.children);//ÊääÊï∞ÁªÑÈáåÈù¢ÁöÑÊï∞ÁªÑÊãÜÂºÄ
        })
        return jQuery(array);
    },

    prev() {
        const array = [];
        this.each((node) => {
            let last = node.previousSibling;
            if (last && last.nodeType === 3) {
                last = last.previousSibling;
            }
            array.push(last);
        })
        return jQuery(array);
    },
    next() {
        const array = [];
        this.each((node) => {
            let next = node.nextSibling;
            if (next && next.nodeType === 3) {
                next = next.nextSibling;
            }
            array.push(next);
        })
        return jQuery(array);
    },
    index() {
        const array = [];
        this.each((node) => {
            const list = Array.from(node.parentNode.childNodes).filter(n => n.nodeType === 1);
            let i;
            for (i = 0; i < list.length; i++) {
                if (list[i] === node) {
                    break
                }
            }
            array.push(i);
        })
        return jQuery(array);
    },
    siblings() {
        const array = [];
        this.each((node) => {
            if (array.indexOf(...Array.from(node.parentNode.childNodes).filter(n => n !== node)) === -1)
                array.push(...Array.from(node.parentNode.childNodes).filter(n => n !== node));
        })
        return jQuery(array);
    }
}